.nav-container {
  box-sizing: border-box;

  position: relative;
  margin: 30px;
  padding: 10px;
  width: max(30%, 300px);


  background: #FFFFFF;
  border: 4px solid #393939;
  /* background-shadow */
  box-shadow: 10px 10px 0px #5EA667;

  transition: 0.5s;
}

.nav-container:hover {
  margin-left: 40px;
}