html {
  background-color: #F3E9E8;
}

.App {
  -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box;    /* Firefox, other Gecko */
  box-sizing: border-box;         /* Opera/IE 8+ */

  font-family: 'JetBrains Mono', 'Consolas', 'Andale Mono WT', 'Andale Mojo', 'Lucida Console', 'Lucida Sans Typewriter', 'DejaVu Sans Mono', 'Bitstream Vera Sans Mono', 'Liberation Mono', 'Nimbus Mono L', Monaco, 'Courier New', Courier, monospace;
  color: #393939;

  padding-bottom: 50px;
}


.name-card {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  flex-wrap: wrap;
  z-index: 2;
}

.my-photo {
  width: min(200px, 100%);
  height: min(200px, 100%);
  border-radius: 50%;
}

.name-card-container {
  flex-grow: 1;
  width: max(70%, 200px);
}